.status {
  background:  $off-white;
  padding-bottom: 100px;
  img.baraban {
    position: absolute;
    top: -350px;
    width: 98%;
    z-index: 3;
    @include r(500) {
      width: 95%;
      top: -250px; } }
  &__block {
    margin-top: 370px;
    @include r(767) {
      margin-top: 0; }
    span {
      font-family: 'PFDinTextCompPro';
      font-size: 300px;
      font-weight: 500;
      color: $pale-gold;
      letter-spacing: -15px;
      display: inline-flex;
      align-items: center;
      line-height: 0.8;
      @include r(767) {
        font-size: 230px; }
      @include r(500) {
        font-size: 153px;
        letter-spacing: -10px;
        margin-left: -20px; } }
    b {
      transform: rotate(-90deg);
      display: inline-block;
      font-family: 'PFDinTextCompPro';
      font-size: 72px;
      font-weight: 500;
      letter-spacing: 16px;
      color: $crimson;
      position: relative;
      top: -40px;
      margin-right: -100px;
      left: -100px;
      opacity: 0;
      @include r(1190) {
        left: 0; }
      @include r(767) {
        left: -48px !important; }
      @include r(500) {
        font-size: 45px;
        letter-spacing: 9px;
        top: -20px; } }
    img {
      margin-top: -50px;
      position: relative;
      right: -100px;
      opacity: 0;
      @include r(1190) {
        right: 0; }
      @include r(950) {
        display: none; } }
    p {
      font-size: 45px;
      text-align: center;
      color: $brownish-grey;
      @include r(600) {
        font-size: 25px; } } } }




