.header {
  padding: 15px 0;
  width: 100%;
  height: 90px;
  z-index: 9;
  transition: all 0.6s;
  position: absolute;
  top: 0px;
}

@media only screen and (max-width: 1024px) {
  .header {
    padding: 15px;
  }
}

.header.active {
  background: #ffffff;
  position: fixed;
}

.header__logo {
  text-align: center;
  display: none;
}

.header__logo img {
  height: 50px;
}

.header .menu-open {
  width: 40px;
  height: 30px;
  padding-top: 15px;
  position: relative;
  display: inline-block;
  cursor: pointer;
  top: 0px;
  transition: all 0.3s;
}

.header .menu-open:hover span {
  background: #8a010e;
}

.header .menu-open:hover span:before {
  background: #8a010e;
}

.header .menu-open:hover span:after {
  background: #8a010e;
}

.header .menu-open span {
  width: 30px;
  height: 2px;
  background: #736b5c;
  display: block;
  transition: all 0.3s;
  border-radius: 20%;
}

.header .menu-open span:before {
  content: '';
  position: absolute;
  left: 0;
  width: 30px;
  height: 2px;
  background: #736b5c;
  transition: all 0.3s;
  top: 7px;
  border-radius: 20%;
}

.header .menu-open span:after {
  content: '';
  position: absolute;
  left: 0;
  bottom: 5px;
  width: 30px;
  height: 2px;
  background: #736b5c;
  transition: all 0.3s;
  border-radius: 20%;
}

.header .menu-open.active span {
  width: 0;
}

.header .menu-open.active span:before {
  width: 30px;
  transform: rotate(225deg);
  top: 14px;
}

.header .menu-open.active span:after {
  transform: rotate(315deg);
  width: 30px;
  bottom: 14px;
}

.header a {
  font-family: 'PFDinTextCompPro';
  font-size: 24px;
  font-weight: 500;
  color: #736b5c;
}

@media only screen and (max-width: 560px) {
  .header a {
    display: none;
  }
}

