.reliability {
  padding-bottom: 100px;
  .swiper {
    position: relative; }
  .swiper-container {
    height: 390px;
    padding: 10px 0;
    .swiper-slide {
      overflow: hidden;
      width: 100%;
      border-radius: 10px;
      position: relative;
      transition: all 0.3s;
      a {
        height: 100%;
        width: 100%; }
      &:before {
        content: '';
        position: absolute;
        width: 100%;
        height: 100%;
        background: rgba(255, 249, 238, 0.5);
        transition: all 0.3s; }
      &:hover {
        box-shadow: 0 5px 10px 0 rgba(138, 1, 14, 0.15);
        &:before {
          visibility: hidden; } } } }
  .swiper-button-next, .swiper-button-prev {
    background: none;
    img {
      width: 40px; } }
  .swiper-button-prev {
    left: -55px; }
  .swiper-button-next {
    transform: rotate(180deg);
    right: -55px; } }
